<template lang="html">
  <div class="">
    <div class="ui teal dividing header">
      <i class="filter icon"></i>
      <div class="content">
        Puit <div class="sub header">Détail puit Id: <u><b><i>{{idpuit}}</i></b></u> Code: <b><u>{{puit.code}}</u></b> </div><i v-if="sending" class="spinner large loading icon" style="position:absolute;top:10px; right:10px;"></i>
      </div>
    </div>
    <div class="ui two aligned grid stackable fluid" style="padding-top:20px;">
      <div class="ten wide column">
        <div id="chartdiv"></div>
       
      </div>

      <div class="six wide column">
        <h3>Ajout d'un echantillon</h3>
        <form class="ui form">
          <div class="field">
            <label>Code</label>
            <input type="text" v-model="echantillon.code">
          </div>
          <div class="field">
            <label>Profondeur debut</label>
            <input type="number" v-model="echantillon.pfd_debut">
          </div>
          <div class="field">
            <label>Profondeur fin</label>
            <input type="number" v-model="echantillon.pfd_fin">
          </div>
          <div class="field">
            <label>Date</label>
            <input type="date" v-model="echantillon.date_prelevement">
          </div>
          <div class="field">
            <label>Couleur</label>
            <input type="text" v-model="echantillon.couleur">
          </div>
          <div class="field">
            <label>structure</label>
            <input type="text" v-model="echantillon.structure">
          </div>
          <div class="field">
            <label>texture</label>
            <input type="text" v-model="echantillon.texture">
          </div>
          <div class="field">
            <label>Autre information</label>
            <textarea rows="2" v-model="echantillon.autre_info"></textarea>
          </div>
          <button v-if="!edit"class="ui primary button" @click.prevent="AddEchantillon" type="submit">Enregistrer</button>
          <button v-if="edit" class="ui default button" @click.prevent="updateEchantillon(echantillon.id)" type="submit">Modifier</button>
          <button v-if="edit" class="ui yellow button" @click.prevent="changeEdit">Annuler</button>
        </form>
      </div>
    </div>
     <div class="six wide column">
            <div class="ui teal dividing header">Liste des echantillons</div>
              <table class="ui celled table">
                    <thead>
                      <tr>
                        <th>Code</th>
                        <th>prof_debut</th>
                        <th>prof_fin</th>
                        <th>Date_prelevement</th>
                        <th>couleur</th>
                        <th>texture</th>
                        <th>structure</th>
                        <th>autre_info</th>
                        <th>Action</th>
                    </tr>
                    </thead>
                    <tbody>
                       
                           <tr v-for="item in listeEchantillon">
                                  <td>
                                    <div class="ui ribbon label">{{ item.code }}</div>
                                  </td>
                                 <td>{{item.pfd_debut}}</td>
                                 <td>{{ item.pfd_fin }}</td>
                                 <td>{{item.date_prelevement}}</td>
                                 <td>{{item.couleur}}</td>
                                 <td>{{item.texture}}</td>
                                 <td>{{item.structure}}</td>
                                 <td><button @click.precent="autreInfo(item)" class="ui circle button"><i class="info green icon">+</i></button></td>
                                 <td>
                                   <div class="ui large buttons">
                                      <button @click.prevent="editEchantillon(item)"class="ui blue button active"> <i  class="edit icon"></i>
                                      </button>
                                         <div class="or"></div>
                                      <button @click.prevent="deleteEnchantillon(item.id)" 
                                        class="ui red button"><i class="remove icon"></i>
                                      </button>
                                          <div class="or"></div>
                                      <button  @click.prevent="AnalyserEchantillon(item)" class="ui green button"><i class="lab icon"></i>Analyser
                                      </button>
                                    </div>
                                  </td>
                            </tr>
                
                    </tbody>
                    <tfoot>
                      <tr><th colspan="9">
                        <div class="ui right floated pagination menu">
                          <a class="icon item">
                            <i class="left chevron icon"></i>
                          </a>
                          <a class="item">1</a>
                          <a class="item">2</a>
                          <a class="item">3</a>
                          <a class="item">4</a>
                          <a class="icon item">
                            
                          </a>
                        </div>
                      </th>
                    </tr></tfoot>
                  </table>
        </div>
<!--     <div class="ui fullscreen modal detailEchantillon">
      
        </div> -->
        <!-- la boite modal permettant de charger les infomations des echantillons -->
       <div class="ui modal autre_info">
              <i class="close icon"></i>
              <div class="header">
                <a class="ui blue ribbon label">{{ detailEchantillon.code }}</a>Autre info sur l'échantillon 
              </div>
              <div>
                <div class="ui form">
                <p  class="disabled field">
                  <textarea>{{detailEchantillon.autre_info}}</textarea>
                </p>
                  
                </div>
              </div>
              <div class="actions">
                <div @click.prevent="closeDetail" class="ui button">Fermer</div>
              </div>
            </div>
  </div>
</template>

<script>
import _ from 'lodash'
import {siteShowUrl,
        echantillonDelete,
        typeAnalyseAddUrl,
        echantillonUrl,
        echantillonsUrl,

        apiDomain,
       getHeader} from '../../../../config'
import toastr from '../../../../assets/toastr.min.js'
import AmCharts from 'amcharts'
var randomColor = require('random-color')

export default {
  data() {
    return {
      idpuit: '',
      idPara: '',
      puit: [],
      parametresA: [],
      edit: false,
      sending: false,
      echa_analyse: {
        IdTypeAnalyse: ''
      },
      echantillon: {
        id: null,
        description: '',
        idpuit: null,
        code: '',
        pfd_debut: '',
        pfd_fin: '',
        date_prelevement: '',
        couleur: '',
        structure: '',
        autre_info: '',
      },
      detailEchantillon: {
        description: '',
        id: null,
        idpuit: null,
        code: '',
        pfd_debut: '',
        pfd_fin: '',
        date_prelevement: '',
        couleur: '',
        structure: '',
        autre_info: '',
      },
      listeEchantillon : [],
      typeAnalyses : [],
      resultatAnalyse:{
        technique:"",
        protocole:"",
        appareillage:"",
        autreInfo:""
      },
      error:{
        technique:false,
        protocole:false,
        appareillage:false,
        autreInfo:false
      }
    };
  },
  components: {
  },
  watch: {
    idPara : function (idPara) {
      this.$http.get(paramsAnalyse+'/'+idPara).then(data => {
        // console.log('arrivé des paramètre')
          // console.log(data.body)
          toastr.success("chargement en cours...")
          this.tab_params = data.body
        
      },data => {
        toastr.error("Impossible de charger les paramètres")
      })
    }
  },
  methods: {
    editEchantillon (echant) {
      this.edit = true
      console.log(echant.id)
      this.echantillon = echant
    },
    changeEdit () {
      console.log('edit')
      this.edit = false
      this.echantillon = []
    },
    AnalyserEchantillon (echantillon){
      var id_ech = echantillon.id
      console.log(id_ech)
       this.$router.push({name: 'analyse.add', params:{idech: id_ech}})
      // this.detailEchantillon = echantillon
      // $('.ui.fullscreen.modal.detailEchantillon')
      //   .modal('show')
      // ;
      // this.$http.get(typeAnalyseAddUrl+'/'+)


    }, //permet d'afficher les informations supplémentaires d'un echantillon
    autreInfo(item) {
      this.detailEchantillon = item
      console.log(this.detailEchantillon.autre_info)
      $('.ui.modal.autre_info')
          .modal('show')
        ;
    },
    updateEchantillon(id) {
        console.log(id)
        console.log("lolpapa")
       this.$http.put(echantillonUrl+'/'+id,this.echantillon).then(data => {
          // console.log(data.body)
          //on remet edit a faux, et on active le bouton d'ajout
          this.edit = false
          this.echantillon =[]
          toastr.success("l'echantillon à été modifié avec success")
          this.getEchantillon()
        
      },data => {
        toastr.error("Impossible de modifier l'echantillon!!")
      })
    },
    deleteEnchantillon(echantillon){
      var x = confirm("Etes vous sur de vouloir supprimer cet échantillon?")
          if(x){
            toastr.error("Suppression en cours...")
            console.log(echantillon)
             this.$http.delete(echantillonUrl+'/'+echantillon,{headers: getHeader()}).then(data => {
                console.log(data.body)
                toastr.success("l'echantillon supprimé avec success")
                this.closeDetail()
                this.getEchantillon()
              
            },data => {
              toastr.error("Impossible de supprimer l'echantillon!!")
            })
          }

    },
    closeDetail () {
      $('.ui.modal')
        .modal('hide')
      ;
    },
    check(){
      if(typeof window.AmCharts !=='undefined'){
        this.showGraph()
      }else{
        toastr.success('connexion')
        setTimeout(this.check,1000);
      }
    },
    getEchantillon(){

      console.log('les puit')
      console.log(this.idpuit)
      console.log(this.idpuit)
      this.$http.get(echantillonsUrl+'/'+this.idpuit,{headers: getHeader()}).then(data => {
        console.log(data.body)
            this.puit = data.body.puit
            this.listeEchantillon =  data.body.echans;
           toastr.success("Chargement des echantillons!!!...................")
          console.log(data.body)
          this.check();
      },data => {
        this.sending = false
        toastr.error("Impossible de charger les échantillons")
      })
    },
    // getPuit () {

    // }
    AddEchantillon(){
      this.echantillon.idpuit = this.$route.params.idpuit
      this.sending = true
      this.$http.post(echantillonUrl,this.echantillon,{headers: getHeader()}).then(data => {
          this.sending = false
           console.log(data.body);
          this.echantillon.description = ''
          this.echantillon.code = ''
          this.echantillon.pfd_debut = ''
          this.echantillon.pfd_fin = ''
          this.echantillon.date_prelevement = ''
          this.echantillon.couleur = ''
          this.echantillon.structure = ''
          this.echantillon.autre_info = ''
          toastr.success("echantillon ajouter avec success")
          this.getEchantillon();
      },data => {
        this.sending = false
        toastr.error("Erreur du serveur, Impossible d'ajouter l'échantillon")
      })
    },
    showGraph () {
      let data = [];
      _.forEach(this.listeEchantillon, (value) => {
          data.push({
            "name": value.code,
            "profondeur_debut": value.pfd_debut,
            "profondeur_fin": value.pfd_fin,
            "data": value,
            "color": randomColor(0.99, 0.99).hexString()
          })
      })
      var chart = window.AmCharts.makeChart("chartdiv", {
      	"theme": "light",
          "type": "serial",
          "dataProvider": data,
          "valueAxes": [{
              "axisAlpha": 0,
              "gridAlpha": 0.1
          }],
          "startDuration": 1,
          "graphs": [{
              "balloonText": "<b>[[category]]</b><br>profondeur debut [[profondeur_debut]]<br>profondeur fin [[profondeur_fin]]",
              "colorField": "color",
              "fillAlphas": 0.8,
              "lineAlpha": 0,
              "openField": "profondeur_debut",
              "type": "column",
              "valueField": "profondeur_fin"
          }],
          "rotate": true,
          "columnWidth": 1,
          "categoryField": "name",
          "categoryAxis": {
              "gridPosition": "start",
              "axisAlpha": 0,
              "gridAlpha": 0.1,
              "position": "left"
          },
          "export": {
          	"enabled": true
           }
      });
      chart.addListener("clickGraphItem", (event) => {
        this.AnalyserEchantillon(event.item.dataContext.data);
      });
    }
  },
  mounted(){
     toastr.options = {"newestOnTop": true,"positionClass": "toast-top-right"}
    this.idpuit = this.$route.params.idpuit
    console.log('idpuit')
    console.log(this.idpuit)  
    this.$http.get(typeAnalyseAddUrl,{headers: getHeader()}).then(data => {
        toastr.success("Connection au serveur établie")
        this.typeAnalyses =  data.body.analyse
        // console.log(data.body)
        console.log('params')
        this.parametresA = data.body.analyse
        console.log(this.parametresA)
    //     console.log(this.parametresA.analyse[1].type)
    },data => {
      this.sending = false
      toastr.error("Erreur  de chargement des analyses")
    })
    this.getEchantillon();
  },
  computed: {

  }
};
</script>

<style lang="css">
  #chartdiv {
    width		: auto;
    height		: 400px;
    font-size	: 11px;
  }
</style>



